  .modal {
    background: white;
    padding: 20px;
    border-radius: 5px;
    width: 500px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  }
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-height: 100vh; /* Ensure it doesn't overflow vertically */
    overflow-y: auto; /* Allows scrolling if content overflows */
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Align modal to the top */
    padding-top: 20px; /* Adds space at the top */
    z-index: 1000;
  }
  
  .modal {
    background: white;
    padding: 20px;
    border-radius: 5px;
    width: 500px;
    max-height: 90vh; /* Limit height to prevent it from going off-screen */
    overflow-y: auto; /* Add scrolling inside the modal */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-top: 10px; /* Adds margin from the top */
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .close-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
  }
  
  .modal-content {
    margin-top: 20px;
  }
  
  .modal-field {
    margin-bottom: 10px;
  }
  
  .divider {
    border: none;
    border-top: 1px solid grey;
    margin: 5px 0;
  }

  .modal-image {
    text-align: center;
    margin-bottom: 15px;
  }
  
  .product-image {
    max-width: 80%;
    height: auto;
    border-radius: 8px;
    object-fit: cover;
  }
  
  .modal-footer {
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
  }

  .modal-option-button {
    padding: 10px 20px;
    margin: 5px;
    border-width: 2px;
    border-color: gray;
    background-color: transparent;
    color: black;
    border-radius: 15px;
    cursor: pointer;
    font-size: 16px;
  }
  